<template>
  <div class="p2">
    <h1 class="uk-text-center uk-margin-large-top uk-margin-large-bottom" style="font-weight: bold;">利润表</h1>
    <div style="width: 900px; margin: 0 auto">
      <div class="uk-text-right">会小企02表</div>
      <div class="uk-flex uk-flex-space-between uk-margin-top-small">
        <span>编制单位:{{com.firm_name}}</span>
        <span>{{com.month}}-{{new Date().getDate()}}</span>
        <span>单位:元</span>
      </div>
    </div>
    <form>
      <table style="margin:0px auto;" cellpadding="0" cellspacing="0" border="1" width="100%">
        <tbody>
        <tr height="30">
          <td width="500" class="uk-text-center">项目</td>
          <td width="30" class="uk-text-center">行次</td>
          <td width="200" class="uk-text-center">本年累计金额</td>
          <td width="200" class="uk-text-center">本月金额</td>
