        <tr height="30">
          <td>一、营业收入</td>
          <td class="uk-text-center">1</td>
          <td class="bg-unwrite"><input name="1_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="1_2" tabindex="1" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　减:营业成本</td>
          <td class="uk-text-center">2</td>
          <td class="bg-unwrite"><input name="2_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="2_2" tabindex="2" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　税金及附加</td>
          <td class="uk-text-center">3</td>
          <td class="bg-unwrite"><input name="3_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="3_2" tabindex="3" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　　其中：消费税</td>
          <td class="uk-text-center">4</td>
          <td class="bg-unwrite"><input name="4_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="4_2" tabindex="4" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　　　　　营业税</td>
          <td class="uk-text-center">5</td>
          <td class="bg-unwrite"><input name="5_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="5_2" tabindex="5" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　　　　　城市维护建设税</td>
          <td class="uk-text-center">6</td>
          <td class="bg-unwrite"><input name="6_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="6_2" tabindex="6" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　　　　　资源税</td>
          <td class="uk-text-center">7</td>
          <td class="bg-unwrite"><input name="7_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="7_2" tabindex="7" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　　　　　土地增值税</td>
          <td class="uk-text-center">8</td>
          <td class="bg-unwrite"><input name="8_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="8_2" tabindex="8" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　　　　　城镇土地使用税、房产税、车船税、印花税</td>
          <td class="uk-text-center">9</td>
          <td class="bg-unwrite"><input name="9_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="9_2" tabindex="9" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　　　　　教育费附加、矿产资源补偿费、排污费</td>
          <td class="uk-text-center">10</td>
          <td class="bg-unwrite"><input name="10_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="10_2" tabindex="10" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　销售费用</td>
          <td class="uk-text-center">11</td>
          <td class="bg-unwrite"><input name="11_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="11_2" tabindex="11" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　其中：商品维护费</td>
          <td class="uk-text-center">12</td>
          <td class="bg-unwrite"><input name="12_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="12_2" tabindex="12" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　　　广告费和业务宣传费</td>
          <td class="uk-text-center">13</td>
          <td class="bg-unwrite"><input name="13_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="13_2" tabindex="13" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　管理费用</td>
          <td class="uk-text-center">14</td>
          <td class="bg-unwrite"><input name="14_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="14_2" tabindex="14" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　其中：开办费</td>
          <td class="uk-text-center">15</td>
          <td class="bg-unwrite"><input name="15_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="15_2" tabindex="15" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　　　　业务招待费</td>
          <td class="uk-text-center">16</td>
          <td class="bg-unwrite"><input name="16_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="16_2" tabindex="16" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　　　　研究费用</td>
          <td class="uk-text-center">17</td>
          <td class="bg-unwrite"><input name="17_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="17_2" tabindex="17" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　财务费用</td>
          <td class="uk-text-center">18</td>
          <td class="bg-unwrite"><input name="18_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="18_2" tabindex="18" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　其中：利息费用(收入以"-"号填列)</td>
          <td class="uk-text-center">19</td>
          <td class="bg-unwrite"><input name="19_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="19_2" tabindex="19" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　加:投资收益(损失以"-"号填列)</td>
          <td class="uk-text-center">20</td>
          <td class="bg-unwrite"><input name="20_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="20_2" tabindex="20" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>二、营业利润(亏损以"-"号填列)</td>
          <td class="uk-text-center">21</td>
          <td class="bg-write"><input name="21_1" readonly value="0.00"></td>
          <td class="bg-write"><input name="21_2" readonly value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　加:营业外收入</td>
          <td class="uk-text-center">22</td>
          <td class="bg-unwrite"><input name="22_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="22_2" tabindex="21" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　　　其中：政府补助</td>
          <td class="uk-text-center">23</td>
          <td class="bg-unwrite"><input name="23_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="23_2" tabindex="22" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　减:营业外支出</td>
          <td class="uk-text-center">24</td>
          <td class="bg-unwrite"><input name="24_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="24_2" tabindex="23" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　　　其中：坏账损失</td>
          <td class="uk-text-center">25</td>
          <td class="bg-unwrite"><input name="25_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="25_2" tabindex="24" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　　　无法收回的长期债券投资损失</td>
          <td class="uk-text-center">26</td>
          <td class="bg-unwrite"><input name="26_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="26_2" tabindex="25" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　　　无法收回的长期股权投资损失</td>
          <td class="uk-text-center">27</td>
          <td class="bg-unwrite"><input name="27_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="27_2" tabindex="26" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　　　自然灾害等不可抗力因素造成的损失</td>
          <td class="uk-text-center">28</td>
          <td class="bg-unwrite"><input name="28_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="28_2" tabindex="27" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　　　　税收滞纳金</td>
          <td class="uk-text-center">29</td>
          <td class="bg-unwrite"><input name="29_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="29_2" tabindex="28" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>三、利润总额</td>
          <td class="uk-text-center">30</td>
          <td class="bg-write"><input name="30_1" readonly value="0.00"></td>
          <td class="bg-write"><input name="30_2" readonly value="0.00"></td>
        </tr>
        <tr height="30">
          <td>　　　减:所得税费用</td>
          <td class="uk-text-center">31</td>
          <td class="bg-unwrite"><input name="31_1" readonly value="0.00"></td>
          <td class="bg-unwrite"><input name="31_2" tabindex="29" value="0.00"></td>
        </tr>
        <tr height="30">
          <td>四、净利润(净亏损以"-"号填列)</td>
          <td class="uk-text-center">32</td>
          <td class="bg-write"><input name="32_1" readonly value="0.00"></td>
          <td class="bg-write"><input name="32_2" readonly value="0.00"></td>
        </tr>
        </tbody>
      </table>
    </form>
    <div style="margin-top: 15px" class="uk-text-center">
      <button @click="saveForm" class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 保存</button>
      <button class="uk-button uk-button-primary" @click="dataRestore()"><i class="uk-icon-close"></i> 删除</button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-file-excel-o"></i> 导出Excel
      </button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-print"></i> 打印</button>
    </div>
  </div>
</template>

<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 8,  //当前表格的名称编号
        setting: null,
        com: []
      }
    },
    computed: {
      com() {
        return this.$store.state.companyCurrent.gscompany
      },
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})
        //获取预设数据
        this.com = this.$store.state.companyCurrent.gscompany
        this.setting = this.$store.state.companyCurrent.content.page8

        // this.setting = {'1_1': 10, '2_1': 5}
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        //21行计算
        this.fn2('21_2', [1, 2, 3, 11, 14, 18, 20], 1)
        //第30行计算
        this.fn2('30_2', [21, 22, 24], 2)
        //第32行计算
        this.fn2('32_2', [30, 31], 3)
        //本年累计计算
        this.fn1(1, 32, [2])
      },
      // (
      // q  要修改值的列
      // r  要修改多少行
      // seed 每行参与计算的列
      // {
      //    1加 a=a+b
      // }
      // )
      fn1(q, r, seed) {
        for (let i = 1; i <= r; i++) {
          var sum = 0
          for (let j = 0; j < seed.length; j++) {
            let m = seed[j]
            let dom = J('input[name=' + i + '_' + m + ']')
            if (dom.val()) {
              if (!isNaN(dom.val())) {
                let val = this.$store.state.companyCurrent.content.page8[i + '_' + q]
                sum += parseFloat(parseFloat(dom.val()) + parseFloat(val))
              }
            }
          }
          J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
        }
      },
      // q  需计算单元格的name
      // gump 参与计算的行
      // type 公式{
      //  1:a=b-c-d-e-f-g+h
      //  2:a=b+c-d
      //  3:a=b-c...
      // }
      fn2(q, gump, type) {
        var sum = parseFloat(J('input[name=' + gump[0] + '_2]').val())
        for (let i = 1; i < gump.length; i++) {
          let dom = J('input[name=' + gump[i] + '_2]')
          if (dom.val()) {
            if (type == 1) {
              if (i == 6) {
                sum += parseFloat(dom.val())
              } else {
                sum -= parseFloat(dom.val())
              }
            } else if (type == 2) {
              if (i == 1) {
                sum += parseFloat(dom.val())
              } else {
                sum -= parseFloat(dom.val())
              }
            } else if (type == 3) {
              sum -= parseFloat(dom.val())
            }
          }
        }

        J('input[name=' + q + ']').val(sum.toFixed(2))
      }
    }
  }
</script>

<style scoped>
  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    max-width: 900px;
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
  }

  input {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    padding: 3px 2px;
    text-align: left;
  }

  .bg-write {
    background: #ccccff;
  }

  .bg-unwrite {
    background: #d6ebff;
  }
</style>



